.c-main{
    height:100vh;
    position:relative;
}
.c-bar{
    width:20px;
    height:100%;
    background-color: #59b256;
    position:absolute;
}
.c-wrapper{
    padding:50px;
    display: flex;
}
.c-left{flex:1}
.c-right{flex:1}

form {
    margin-top:20px;
}
input {
    width:50%;
    height: 50px;
    border:none;
    border-bottom: 1px solid #000;
    margin:10px 0;
    font-size:14px;
    padding-left:10px;
}

textarea{
    width:100%;
    margin:10px 0;
    font-size:14px;
    padding-left:10px;
}

button{
    border:none;
    padding:15px;
    background-color: #59b256;
    color:#fff;
    font-weight:500;
    font-size:18px;
    cursor: pointer;
}